import React, { Component } from 'react';
import { connect } from 'react-redux';

import { addTast, removeTask, completeTask, changeFilter } from '../../actions/actionCreator';

import ToDoInput from '../../components/todo-input/todo-input';
import ToDoList from '../../components/todo-list/todo-list';
import Footer from '../../components/footer/footer';

import './todo.css';

class ToDo extends Component {

  state = {
    // activeFilter: 'all',
    taskText: ''
  }

  handleInputChange = ({ target: { value } }) => {
    this.setState({
      taskText: value,
    })
  }

  // 1)  handleKeyPress    addTast
  handleKeyPress = ({ key }) => {
    const { taskText } = this.state;
    if (taskText.length > 3 && key === 'Enter') {
      //   addTast  connect,        - this.props
      const { addTast } = this.props;
      //    3 :
      addTast((new Date()).getTime(), taskText, false);

      this.setState({
        taskText: '',
      })

    }

  }
  // 2) addTast    

  // filterTasks  -    -
  //   - -  -  - new 
  filterTasks = (tasks, activeFilter) => {
    switch (activeFilter) {
      case 'completed':
        return tasks.filter(task => task.isCompleted);
      case 'active':
        return tasks.filter(task => !task.isCompleted);
      default:
        return tasks;
    }
  }

  getActiveTasksCounter = tasks => tasks.filter(task => !task.isCompleted).length;

  render() {
    const { taskText } = this.state;
    const { tasks, removeTask, completeTask, filters, changeFilter } = this.props; //     removeTask,   ..
    // ..  removeTask      <ToDoList/>
    const isTasksExist = tasks && tasks.length > 0;
    const filteredTasks = this.filterTasks(tasks, filters);
    const taskCounter = this.getActiveTasksCounter(tasks);

    return (
      <div className="todo-wrapper">
        <ToDoInput onKeyPress={this.handleKeyPress} onChange={this.handleInputChange} value={taskText} />
        {isTasksExist && <ToDoList tasksList={filteredTasks} removeTask={removeTask} completeTask={completeTask} />}
        {isTasksExist && <Footer activeFilter={filters} changeFilter={changeFilter} amount={taskCounter} />}
      </div>
    );
  }
}
// 6) -,  -  state,    -     state-,
//   ,   
export default connect(state => ({
  tasks: state.tasks,
  filters: state.filters,
}), { addTast, removeTask, completeTask, changeFilter })(ToDo);
//       , -  2    connect
//     Redux-state       - tasks,
// -  - state.tasks



